import React from "react";
import { Link } from "react-router-dom";
import style from "./Cartes.module.css";

function CarteNav({ navigation }) {
  const area = navigation[1] !== null ? "/" + navigation[1] : "";
  const nom = "/" + navigation[0];
  const lien = area + nom;
  return (
    <article className={style.nav}>
      <div className={style.form}>
        <Link to={lien} className={style.pointer}>
          {navigation[0]}
        </Link>
      </div>
    </article>
  );
}

export default CarteNav;
