import React, { useEffect, useState } from 'react'
import CarteMenuNav from './CarteMenuNav'
import CarteNav from './CarteNav'
import style from './Navigation.module.css'
import { navigation } from './useNavigation'

export default function NavigationMenu({
  fetchRequest,
  contentObject,
  resources
}) {
  const [select, setSelect] = useState(-1)
  const [load, loader] = useState(false)
  useEffect(() => {
    navigation.load(fetchRequest, contentObject, loader, resources)
  }, [])
  const navListe = navigation.groupe
  return (
    <div className={style.displayNav}>
      {load
        ? Object.entries(navListe).map((groupe, index) => {
            if (groupe[1].length === 1) {
              return <CarteNav navigation={groupe[1][0]} key={index} />
            } else {
              return (
                <div key={index} className={style.displayNav}>
                  {select !== index ? (
                    <CarteMenuNav
                      setSelect={setSelect}
                      index={index}
                      groupe={groupe[0]}
                    />
                  ) : (
                    groupe[1].map((nav, indexM) => {
                      return <CarteNav key={indexM} navigation={nav} />
                    })
                  )}
                </div>
              )
            }
          })
        : null}
    </div>
  )
}
