import React from "react";
import style from "./Cartes.module.css";

function CarteMenuNav({ groupe, index, setSelect }) {
  return (
    <article className={style.nav}>
      <div className={style.form}>
        <div
          onClick={() => {
            setSelect(index);
          }}
          className={style.pointer}
        >
          {groupe}
        </div>
      </div>
    </article>
  );
}

export default CarteMenuNav;
