export const getNavigation = (data, resources) => {
  let navTab = {}
  if (data !== undefined) {
    for (let d of data) {
      if (!navTab[resources[d.groupe]]) {
        navTab[resources[d.groupe]] = []
      }
      navTab[resources[d.groupe]] = [
        ...navTab[resources[d.groupe]],
        [resources[d.nom], d.adresse]
      ]
    }
  }
  return navTab
}
