import React from 'react'
import { BrowserRouter, Redirect, Route } from 'react-router-dom'
import NavigationMenu from './Navigation'

export const NavigationDisplay = ({
  fetchRequest,
  contentObject,
  resources
}) => {
  return (
    <NavigationMenu
      fetchRequest={fetchRequest}
      contentObject={contentObject}
      resources={resources}
    />
  )
}

export const NavigationBrowser = ({ children }) => {
  return <BrowserRouter children={children}></BrowserRouter>
}

export const NavigationRouter = ({ children, link }) => {
  return <Route path={link} children={children}></Route>
}

export const NavigationRouterExact = ({ children, link }) => {
  return <Route exact path={link} children={children}></Route>
}

export const NavigationRedirect = ({ link }) => {
  return <Redirect to={link} />
}
